.futurecast {
    flex: 5;
}

.list {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: space-between;
    width: 98.5%;
    height: 26rem;
}

.listItem {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(101,43,215,.75);
    color: white;
    height: 18%;
    border-radius: .3rem;
}

.listItem h4 {
    width: 5rem;
    font-size: 1.2rem;
    margin-left: 1rem;
    font-weight: 500;
    letter-spacing: .1rem;
}

.listItem img {
    width: 3rem;
    opacity: .75;
}

.highTemp, .lowTemp, .rainChance {
    width: 5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.highTemp h5, .lowTemp h5, .rainChance h5 {
    font-size: 1.5rem;
    font-weight: 500;
}

.highTemp span, .lowTemp span, .rainChance span {
    font-size: .88rem;
    opacity: .75;
    letter-spacing: .1rem;
}

/*-- media queries --*/
@media(max-width: 1025px) {
    .futurecast {
        width: 95%;
        margin: 1rem auto 0;
    }

    .list {
        width: 100%;
    }
}

@media(max-width: 825px) {
    .rainChance {
        display: none;
    }
}